html, body, div, span, applet, object, iframe, table, caption, tbody, tfoot, thead, tr, th, td,
del, dfn, em, font, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var,
h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code,
dl, dt, dd, ol, ul, li, fieldset, form, label, legend {
  vertical-align: baseline;
  font-family: inherit;
  font-weight: inherit;
  font-style: inherit;
  font-size: 100%;
  outline: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

/* remember to define focus styles! */
:focus {
  outline: 0;
}

body {
  background: white;
  line-height: 1;
  color: black;
}

ol, ul {
  list-style: none;
}

/* tables still need cellspacing="0" in the markup */
table {
  border-collapse: separate;
  border-spacing: 0;
}

caption, th, td {
  font-weight: normal;
  text-align: left;
}

/* remove possible quote marks (") from <q> & <blockquote> */
blockquote:before, blockquote:after, q:before, q:after {
  content: "";
}

blockquote, q {
  quotes: "" "";
}

* {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.clearfix:after {
  content: "";
  display: table;
  clear: both;
}

.wrapper {
  max-width: 1210px;
  margin: 0 auto;
  padding-left: 0px;
  padding-right: 0px;
  flex-wrap: wrap;
}

@media (max-width: 1250px) {
  .wrapper {
    max-width: 900px;
  }
}

@font-face {
  font-family: "Ubuntu-R";
  src: url("../fonts/UbuntuRegular.eot");
  src: url("../fonts/UbuntuRegular.eot?#iefix") format("embedded-opentype"), url("../fonts/UbuntuRegular.woff") format("woff"), url("../fonts/UbuntuRegular.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: "Ubuntu-L";
  src: url("../fonts/UbuntuLight.eot");
  src: url("../fonts/UbuntuLight.eot?#iefix") format("embedded-opentype"), url("../fonts/UbuntuLight.woff") format("woff"), url("../fonts/UbuntuLight.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: "Ubuntu-M";
  src: url("../fonts/UbuntuMedium.eot");
  src: url("../fonts/UbuntuMedium.eot?#iefix") format("embedded-opentype"), url("../fonts/UbuntuMedium.woff") format("woff"), url("../fonts/UbuntuMedium.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: "Ubuntu-B";
  src: url("../fonts/UbuntuBold.eot");
  src: url("../fonts/UbuntuBold.eot?#iefix") format("embedded-opentype"), url("../fonts/UbuntuBold.woff") format("woff"), url("../fonts/UbuntuBold.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

.language {
  flex-grow: 0;
  font-family: "Ubuntu-L";
}

.language__selected {
  background: url(../img/sprite.png) 0 0 no-repeat;
  border: none;
  display: block;
  width: 100%;
  padding-left: 10px;
  padding-right: 15px;
  background-position: 40px -487px;
}

.language__wrapper {
  position: relative;
  width: 100%;
}

.language__items {
  cursor: pointer;
  position: absolute;
  display: none;
  left: 0px;
  top: 24px;
  right: 0px;
  z-index: 99;
  padding: 0px;
}

.language__item {
  text-align: center;
  width: 100%;
}

.language__item a {
  width: 100%;
  display: block;
  padding: 10px 0px;
  text-decoration: none;
}

.btn {
  font-size: 16px;
  -webkit-border-radius: 26px;
  text-transform: uppercase;
  font-family: "Ubuntu-R";
  -moz-border-radius: 26px;
  border-radius: 26px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 18px 32px;
  text-decoration: none;
}

.btn-green {
  background: #4fdf81;
  color: #ffffff;
}

.menu__link {
  font-size: 18px;
  color: #414141;
  text-decoration: none;
  text-transform: uppercase;
  font-family: "Ubuntu-L";
  padding: 0 20px;
  letter-spacing: 1px;
}

.logo {
  flex-grow: 1.2;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 7.5px;
  margin-right: 7.5px;
}

nav {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 7.5px;
  margin-right: 7.5px;
  flex-grow: 1;
}

.header {
  min-height: 78px;
  display: flex;
  flex-wrap: wrap;
  margin-left: -7.5px;
  margin-right: -7.5px;
  align-items: center;
  justify-content: space-between;
}

.header > * {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 7.5px;
  margin-right: 7.5px;
}
